<template>
  <div class="search" :class="{ show: isShow }">
    <img class="icon" :src="imgurl" />
    <input
      :value="inputStr"
      placeholder="请输入搜索"
      @keydown.enter="gotosearch"
      @focus="bgcolor(true)"
      @focusout="bgcolor(false)"
    />
    <img class="icon" src="../assets/s.svg" @click="gotosearch" />
  </div>
</template>

<script>
import bd from "../assets/baidu.svg";
import bing from "../assets/bing.png";
import google from "../assets/google.svg";

export default {
  created() {
    document.addEventListener("keydown", this.eventF);
  },
  data() {
    return {
      isShow: false,
      type: 3,
      imgurl: bd,
      inputStr: "",
    };
  },
  methods: {
    eventF(e) {
      if ("ArrowUp" === e.code) {
        this.switchType(true);
      } else if ("ArrowDown" === e.code) {
        this.switchType(false);
      } else if ("KeyI" === e.code) {
        const ele = document.getElementsByTagName("input")[0];
        if (ele != document.activeElement) ele.focus();
      } else if ("Escape" === e.code) {
        this.inputStr = "";
        this.isShow = false;
      } else if ("ArrowLeft" === e.code) {
        this.$router.push("/n");
      } else if ("ArrowRight" === e.code) {
        this.$router.push("/t");
      }
    },
    bgcolor(boo) {
      this.isShow = boo;
    },
    gotosearch() {
      if (this.inputStr === "") return;
      if (this.type === 1) {
        window.location.href = "https://cn.bing.com/search?q=" + this.inputStr;
      } else if (this.type === 2) {
        window.location.href =
          "https://www.google.com/search?q=" + this.inputStr;
      } else {
        window.location.href = "https://www.baidu.com/s?wd=" + this.inputStr;
      }
    },
    switchType(boo) {
      if (boo) {
        this.type = this.type === 3 ? 1 : this.type + 1;
      } else {
        this.type = this.type === 1 ? 3 : this.type - 1;
      }
      if (this.type === 1) {
        this.imgurl = bing;
      } else if (this.type === 2) {
        this.imgurl = google;
      } else {
        this.imgurl = bd;
      }
    },
  },
};
</script>

<style scope>
.search {
  width: 330px;
  height: 30px;
  display: grid;
  border: 1px solid #d3d3b0;
  border-radius: 6px;
  position: absolute;
  grid-template-columns: 30px 270px 30px;
  align-items: center;
  left: 50%;
  top: 40%;
  transform: translate(-50%, -50%);
}
.icon {
  width: 18px;
  height: 18px;
  transform: translate(50%);
}
input {
  outline: none;
  border: none;
  background-color: transparent;
  font-size: 10px;
}
.show {
  background-color: aliceblue;
}
</style>